import React, { PropTypes } from 'react'
import { Form, Input,Select, DatePicker, Button,Modal,Cascader } from 'antd'
import moment from 'moment'

import 'moment/locale/zh-cn';
moment.locale('zh-cn');
const FormItem = Form.Item
const Option = Select.Option

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
}

const modal = ({
  visible,
  type,
  item = {},
  onOk,
  onOkAdd,
  onCancel,
  ModalList,
  form: {
    getFieldDecorator,
    validateFields,
    getFieldsValue
  },
  productTypes:{
    sWebProvinceList,
    custormerList
    }
}) => {
  function handleOk () {
    validateFields((errors) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        key: item.key
      }
      onOk(data)
    })
  }
  function handleOkAdd(){
    validateFields((errors) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        key: item.key
      }
      onOkAdd(data)
    })
  }
  function inputOnBlur(){

  }

  function onChange(value) {
    console.log(value);
  }

  const modalOpts = {
    title: `${type === 'create' ? '新增联系人' : '修改联系人'}`,
    visible,
    onOk: handleOk,
    onOkAdd:handleOkAdd,
    onCancel,
    wrapClassName: 'vertical-center-modal'
  }

  //客户
  const customers = [];
  custormerList&&custormerList.map((item)=> {
    return (
      customers.push(<Option key={item.customerName} value={item.customerId.toString()}>{item.customerName}</Option>)
    )
  })
  const nextFollowTime = moment(new Date(ModalList.birthday));
  return (
    <Modal {...modalOpts}footer={[
      <Button key="cancel" size="default" onClick={onCancel}>取消</Button>,
      <Button key="submit" type="primary" size="default" onClick={handleOk}>保存</Button>,
      <Button key="submitAndAdd" type="primary" size="default" onClick={handleOkAdd}>保存并新增联系人</Button>
    ]}>
      <Form horizontal>
        <FormItem>
          {getFieldDecorator('id', {
            initialValue: ModalList.id ||'',
          })(<Input type="hidden"/>)}
        </FormItem>
        <FormItem label='姓名：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('contactName', {
            initialValue:ModalList.contactName ||'',
            rules: [
              {
                required: true,
                message: '姓名未填写'
              }
            ]
          })(<Input />)}
        </FormItem>
        <FormItem label="对应客户:" hasFeedback {...formItemLayout}>
          {getFieldDecorator('customerId',{
            initialValue: ""+ModalList.customerId+"" ||'',
            rules: [
              {
                required: true,
                message: '对应客户未填写'
              }
            ]
          })(
            <Select>
              {customers}
            </Select>
          )}
        </FormItem>
        <FormItem label='部门：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('depart', {
            initialValue: ModalList.depart ||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label='职务：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('jobTitle', {
            initialValue: ModalList.jobTitle ||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label='手机号 ' hasFeedback {...formItemLayout}>
          {getFieldDecorator('phone', {
            initialValue: ModalList.phone ||'',
            rules: [
              {
                required: true,
                message: '手机号未填写'
              }
            ]
          })(
            <Input />
          )}
        </FormItem>
        <FormItem label='电话' hasFeedback {...formItemLayout}>
          {getFieldDecorator('tel', {
            initialValue: ModalList.tel ||'',
          })(
            <Input />
          )}
        </FormItem>
        <FormItem label='邮箱：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('email', {
            initialValue: ModalList.email ||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label='网址：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('url', {
            initialValue: ModalList.url ||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label='地区：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('region', {
            initialValue: ModalList.region ||'',
            rules: [
              {
                required: true,
                message: '地区要填写'
              }
            ]
          })(
            <Cascader options={sWebProvinceList} onChange={onChange} placeholder="请选择地区" />
          )}
        </FormItem>
        <FormItem label='地址：' hasFeedback {...formItemLayout}>
          {getFieldDecorator('address', {
            initialValue: ModalList.address ||'',
            rules: [
              {
                required: true,
                message: '姓名未填写'
              }
            ]
          })(<Input />)}
        </FormItem>
        <FormItem label="邮编" hasFeedback {...formItemLayout}>
          {getFieldDecorator('post', {
            initialValue: ModalList.post ||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label="性别:" hasFeedback {...formItemLayout}>
          {getFieldDecorator('sex',{
            initialValue: ModalList.sex ||'',
          })(
            <Select>
              <Option value="1">男</Option>
              <Option value="2">女</Option>
            </Select>
          )}
        </FormItem>
        <FormItem label="生日:" hasFeedback {...formItemLayout}>
          {getFieldDecorator('birthday',{
            initialValue: nextFollowTime ||'',
          })(
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm:ss"
              placeholder="选择时间"
            />
          )}
        </FormItem>
        <FormItem label="备注" hasFeedback {...formItemLayout}>
          {getFieldDecorator('memo', {
            initialValue: ModalList.memo ||'',
          })(
            <Input type="textarea" rows={4} />
          )}
        </FormItem>
      </Form>
    </Modal>
  )
}

modal.propTypes = {
  visible: PropTypes.any,
  form: PropTypes.object,
  item: PropTypes.object,
  onOk: PropTypes.func,
  onCancel: PropTypes.func
}

export default Form.create()(modal)



