import React, { PropTypes } from 'react'
import { routerRedux } from 'dva/router'
import { connect } from 'dva'
import ContactsList from '../components/contacts/list'
import ContactsSearch from '../components/contacts/search'
import ContactsModal from '../components/contacts/modal'

function Contacts({ location, dispatch, contacts}) {
  const { loading, list, pagination, currentItem, ModalVisiblecontact, modalType,productTypes ,ModalList } = contacts
  const { field, keyword } = location.query

  const contactModalProps = {
    item: modalType === 'create' ? {} : currentItem,
    type: modalType,
    visible: ModalVisiblecontact,
    ModalList:ModalList,
    productTypes:productTypes,
    onOk (data) {
      dispatch({
        type: `contacts/${modalType}`,
        payload:{
          data,
          add:false
        }
      })
    },
    onOkAdd (data) {
      dispatch({
        type: `contacts/${modalType}`,
        payload: {
          data,
          add:true
        }
      })
    },
    onCancel () {
      dispatch({
        type: 'contacts/hideModalcontact'
      })
    }
  }

  const contactListProps = {
    dataSource: list,
    loading,
    pagination: pagination,
    onPageChange (page) {
      const { query, pathname } = location
      dispatch(routerRedux.push({
        pathname: pathname,
        query: {
          ...query,
          pageNo: page.current,
          pageSize: page.pageSize
        }
      }))
    },
    onDeleteItem (id) {
      dispatch({
        type: 'contacts/delete',
        payload: id
      })
    },
    onEditItem (item) {
      dispatch({
        type: 'contacts/editQuery',
        payload: {
          modalType: 'update',
          id: item
        }
      })
    }
  }

  const contactSearchProps = {
    field,
    keyword,
    onSearch (fieldsValue) {
      fieldsValue.keyword.length ? dispatch(routerRedux.push({
        pathname: '/admin/contacts',
        query: {
          field: fieldsValue.field,
          keyword: fieldsValue.keyword
        }
      })) : dispatch(routerRedux.push({
        pathname: '/admin/contacts'
      }))
    },
    onAdd () {
      dispatch({
        type: 'contacts/showModalcontactAdd',
        payload: {
          modalType: 'create'
        }
      })
    }
  }

  const ContactsModalGen = () =>
    <ContactsModal {...contactModalProps} />

  return (
    <div className='content-inner'>
      <ContactsSearch {...contactSearchProps} />
      <ContactsList {...contactListProps} />
      <ContactsModalGen />
    </div>
  )
}

Contacts.propTypes = {
  customers: PropTypes.object,
  location: PropTypes.object,
  dispatch: PropTypes.func
}

function mapStateToProps ({ contacts}) {
  return { contacts}
}

export default connect(mapStateToProps)(Contacts)



